.hero {
  padding-top: 65px;
  padding-bottom: 20px;
  text-align: center;
  background: url('../media/images/hero-bg.png');
  background-size: cover;

  &__wrap {
    display: inline-block;
    text-align: center;
    width: 700px; }

  &__title {
    font-size: 46px;
    font-weight: 400; }

  &__descr {
    margin-top: 15px;
    margin-bottom: 30px;
    font-size: 16px; }

  .search {
    box-shadow: -2px 8px 5px -4px rgba(224,224,224,1); } }
